.root {
    display: flex;
    width: 100%;
    flex-direction: column;
    position: relative;
  
    .label {
      padding-bottom: 5px;
    }
  
    .wrapper {
      // display: flex;
      // flex-direction: row;
  
      .read-only-value {
        font-weight: unset;
        padding: 5px;
        min-height: 31px;
      }
  
      .autocomplete-field {
        flex: 1;
        margin-bottom: 23px;
        display: flex;
        flex-direction: column;
  
        .form-control {
          width: 100%;
        }
  
        .input {
          width: 100%;
          input {
            padding: 10px;
          }
        }
  
        .suggestions {
          z-index: 2;
          position: absolute;
          margin-top: 40px;
          min-width: 100%;
        }
  
        border: 1px solid transparent;
  
        &.focus {
          border-color: green;
        }
  
        // Errors
        &.has-error {
          border-color: red;
          margin-bottom: unset;
        }
      }
  
      span.error {
        padding-top: 1px;
        color: red;
      }
    }
  }
  