.drawer-content {
  margin: 20px 0px 20px 0px;

  .drawer-account-value-field{
    font-weight: bolder;
  }
}


.content-header-details {
  display: flex;

  .fields {
    margin-right: 20px;
    flex: 1;
  }

  .values {
    margin-right: 8px;
    .credit-memo-values{
      font-weight: bolder;
    }
  }

  .status {
    color: #fd5451;
  }
}
//backdrop
.backdrop {
  z-index: 5000;
  color: '#fff';
}
.export-view-billing-cycle-wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 10px;

  .export-view-content {
    .export-icon {
      color: #3ab77d;
      transform: scaleX(-1);
      -moz-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
      margin-bottom: -7px;
      margin-right: 5px;
    }

    .export-link {
      margin-top: -10px;
      margin-right: 15px;
      color: #3ab77d;
    }

    .view-icon {
      color: #3ab77d;
      margin-bottom: 0;
      margin-right: 8px;
    }

    .view-link {
      color: #3ab77d;
      margin-right: 20px;
    }
  }
}

.button {
  background-color: rgb(136, 132, 132);
  display: flex;
  padding: 10px;
  z-index: 2;
  .item-right-side {
    border-radius: 3pt;
    margin-left: auto;
  }
  .button-approve {
    color: white;
    background-color: #3ab77d;
    width: 150px;
    margin-right: 10px;
    text-transform: none;
    text-transform: none;
    &:hover {
      background-color: #0b4930;
    }
  }
  .button-void {
    color: white;
    background-color: #c53a2e;
    margin-right: 10px;
    width: 150px;
    text-transform: none;
    &:hover {
      background-color: #490b0b;
    }
  }

  .button-negative {
    color: white;
    background-color: #353837;
    border-radius: 3pt;
    width: 300 !important;
    text-transform: none;
    &:hover {
      background-color: #8b918f;
    }
  }
}
