.headerbar {
  background-color: $color-12;
}

.link {
  text-decoration: none;
  color: $color-3;
  font-weight: bold;
}

.sidescrollButton {
  font-size: 0.7rem;
}

.MuiTableCell-sizeSmall {
  padding: 2px 2px 2px 2px !important;
}

.rowCollapseIcon {
  padding: 0, 0, 0, 0 !important;
  svg {
    font-size: 0.95rem;
  }
}

.headerCollapseIcon {
  button {
    padding: 3px 2px 2px 5px !important;
  }
  svg {
    font-size: 1.1em;
  }
}

.headerCell {
  font-size: 0.96rem !important;
  padding: 2px 3px 2px 3px !important;
  min-width: 150px !important;
  max-width: 300px;
}

.headerCellMonetary {
  font-size: 0.96rem !important;
  padding: 2px 30px 2px 1px !important;
  min-width: 150px !important;
  max-width: 300px;
}

.contentCell {
  font-size: 0.8rem !important;
  padding: 13px 1px 13px 1px !important;
  width: fit-content !important;
  max-width: 300px;
}
.contentCellMonetary {
  font-size: 0.8rem !important;
  padding: 2.5px 30px 2.5px 1px !important;
  width: fit-content !important;
  max-width: 300px;
  text-align: right;
}

.headerCollapseButton {
  padding: 3px 15px 3px 15px !important;
  width: 20px;
  max-width: 40px !important;
  // text-align: center !important;
  font-size: 0.3rem;
}

.rowCollapseButton {
  padding: 2px 10px 2px 2px !important;
  width: 2px;
  max-width: 2px !important;
  text-align: center !important;
  font-size: 0.5rem;
}

.sort-icon {
  height: 20px;
  font-size: 13px;
  margin-left: 20px;
  cursor: pointer;
}

.pagination {
  width: 20%;
  margin-left: 80%;
}

.normal_text {
  font-weight: normal !important;
}

.boldface {
  font-weight: bold !important;
}

.editRow {
  background-color: #feb8b7 !important;
}

.vertical { 
  border-left: 2px solid #b1b1b1; 
  position: relative;
  left: 40%;
} 

.corner { 
  border-left: 2px solid #b1b1b1; 
  border-bottom: 2px solid #b1b1b1;
  width: 15px;
  position: relative;
  left: 40%;
} 