.root {
  display: flex;
  position: relative;
  background: #525659;

  .controls {
    display: flex;
    padding: 10px;
    position: absolute;
    min-height: 60px;
    width: 100%;
    z-index: 1;
    background-color: #323639;
    .left-pane {
      float: left;
    }
    .item-right-side {
      border-radius: 3pt;
      margin-left: auto;
    }

    .control-left {
      margin-left: 35px;
      color: white;
      font-size: 17px;
    }
    .search-span {
      margin-left: -8px;
      position: absolute;
    }
    .control {
      margin-left: -8px;
      color: white;
      font-size: 17px;
    }
    .page-number {
      margin: 12px 0px 0px 20px;
      color: #dddddd;
      font-weight: 600;
    }

    .page {
      margin: 12px 0px 0px 20px;
      color: gray;
      float: left;
    }

    .bar {
      margin: 0px 0px -8px 2px;
      padding: 1px;
      color: white;
      font-size: 17px;
    }

    .zoom {
      margin-left: 60px;
      color: white;
      font-size: 17px;
    }

    .zoomout {
      margin-left: -10px;
      color: white;
      font-size: 17px;
    }

    .back {
      margin-left: -13px;
      color: white;
      font-size: 17px;
    }

    .autozoom {
      margin-left: 2px;
      color: white;
      font-size: 12px;
    }

    .fullscreen {
      margin-left: 100px;
      // left: 30%;
      justify-content: flex-end;
      color: white;
      font-size: 12px;
    }
  }

  .pdf {
    display: flex;
    flex: 1;
    justify-content: center;
    padding: 10px;
    padding-top: 70px;
    overflow: auto;
  }

  .file-name {
    color: #dddddd;
    font-weight: 600;
    margin-right: 50px;
  }
  .middle {
    margin-left: auto;
    position: relative;
  }
  .far-right{
    float: right;
    position: relative;
    margin-left: auto;
  }
}

.pdfDocument {
  visibility: hidden;
}
