.formContainer {
  flex: 0.1 0.5em 20em;
  border-radius: 3px;
  width: 100%;
  height: auto;
  right: 0;
  top: 0;
  position: relative;

  .stepper-container {
    display: flex;
    justify-content: space-around;

    .step-wrapper-container {
      flex: 1 1 1em;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;

      .stepNumber {
        flex: 1em 1em 1em;
        border-radius: 80%;
        width: 15px;
        height: 15px;
        padding: 3px;
        z-index: 1;
        border: 1px solid lightgray;
        background-color: lightgray;
        border-style: dashed solid;
        box-shadow: inset 0 2px 3px lightgray, inset 0 -2px 3px lightgray, 0 1px 1px lightgray;
      }

      .stepNumberActive {
        flex: 1em 1em 1em;
        border-radius: 80%;
        width: 15px;
        height: 15px;
        padding: 3px;
        z-index: 1;
        border: 1px solid lightgreen;
        background-color: lightgreen;
        border-style: dashed solid;
        box-shadow: inset 0 2px 3px lightgreen, inset 0 -2px 3px lightgreen, 0 1px 1px lightgreen;
        background-color: lightgreen;
      }

      .stepNumberActive.highlight {
        border-color: #3ab77d;
        box-shadow: inset 0 2px 3px #3ab77d, inset 0 -2px 3px #3ab77d, 0 1px 1px #3ab77d;
        background-color: #3ab77d;
      }

      .stepDetails {
        flex: 1 1 0.2em;
        font-size: 0.8em;
        margin-top: 4px;
        text-align: center;
      }

      .highlight {
        color: #3ab77d;
        font-weight: unset;
      }

      .stepDetailsActive {
        flex: 1 1 0.2em;
        margin-top: 4px;
        font-size: 0.8em;
        text-align: center;
        font-weight: bold;
      }

      .step-divider {
        flex: 1 0.52 0.5em;
        height: 2px;
        background-color: #bdbdbdbd;
        position: absolute;
        width: 100%;
        top: 20%;
        left: 50%;
      }

      .step-divider.highlight {
        background-color: #3ab77d;
      }
    }
  }
}
