@import "styles/color.scss";

.alert {
  max-width: 400px;
  .title,
  .content {
    padding: 10px;
    color: rgba(0, 0, 0, 0.8);
  }
  .title {
    .text {
      font-weight: normal;
    }
  }
  .content {
    .details {
      .details-wrapper {
        &:not(:last-child) {
          margin-bottom: 15px;
        }
        letter-spacing: 0.2px;
      }
    }
  }
  .control {
    .control-wrapper {
      .control-container {
        button {
          text-transform: none;
        }
        .button-outline-success {
          border-color: $color-3;
          color: $color-3;
          width: 100%;
        }
        .button-fill-success {
          background: $color-3;
          border-color: $color-3;
          color: $color-22;
          width: 100%;
        }
      }
    }
  }
}
