@import "styles/color.scss";

.root-assign-dialog {
  padding: 30px;
  width: 400px;
  * {
    user-select: none;
    letter-spacing: 0.2px;
  }

  & > .title {
    margin-bottom: 15px;
    & > .title-wrapper {
      position: relative;

      & > .title-action {
        position: absolute;
        top: -20px;
        right: -20px;
        cursor: pointer;
      }
    }
  }

  & > .details {
    margin-bottom: 15px;
  }

  & > .info {
    margin-bottom: 25px;
  }

  & > .action {
    button {
      text-transform: unset !important;
      width: 100%;
      padding: 10px;
    }
    .assign {
      background: $color-13;
      color: white;
    }
    .cancel {
      border-color: $color-13;
      color: $color-13;
    }
  }
}

.root-assign-success {
  padding: 30px;
  width: 400px;
  * {
    user-select: none;
    letter-spacing: 0.2px;
  }

  & > .title {
    margin-bottom: 15px;
    & > .title-wrapper {
      position: relative;

      & > .title-action {
        position: absolute;
        top: -20px;
        right: -20px;
        cursor: pointer;
      }
    }
  }

  & > .details {
    margin-bottom: 30px;
  }

  & > .action {
    button {
      text-transform: unset !important;
      width: 100%;
      padding: 10px;
    }
    .confirm {
      background: $color-13;
      color: white;
    }
  }
}

.root-user-info {
  .container {
    padding: 20px 0 20px 30px;
  }
  .table {
    padding-right: 10px;
    & th {
      background: #e0e0e0 !important;
      padding-bottom: 0;
    }

    & tr:nth-child(even) {
      & td {
        background: #f5f5f5 !important;
      }
    }
    .no-overflow {
      max-width: 1px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    & .table-header {
      max-height: 25px;
    }
  }
  .close-button {
    text-align: right;
    padding-right: 15px;
    .close-icon {
      cursor: pointer;
    }
  }
  .padded-table {
    padding-top: 20px;
  }
  .analyst-name { font-weight: 600; }
  .sort-field {
    cursor: pointer;
    user-select: none;
    .sort-name { white-space: nowrap; }
    .sort-wrapper {
      .sort-container {
        &.selected {
          color: rgba(0,0,0,.8) !important;
        }
        height: 0;
        color: rgba(0,0,0,.4);
      }
    }
  }
}
