
.dropdown-option {
  min-width: 500px;
  max-width: 500px;
  margin-left: 0;
}

.show-info {
  max-width: 500px;
  .content-header-details {
    display: flex;
    margin-top: 3px;

    .fields {
      max-width: 200px;
      text-align: left;
      flex: 1;
      margin-right: 20px;
    }

    .values {
      text-align: left;
    }
  }
}

.footer-divider {
  min-height: 100px;
}

.checkbox_option {
  max-width: 300px;
  svg {
    background: rgb(80, 80, 80);
    border-radius: 6px;
    path {
      color: white;
      height: 100%;
    }
  }
}

.checkbox_option2 {
  width: 28px;
  height: 28px;
  svg {
    border-radius: 6px;
    border: 1.5px solid black;
    // background: rgb(80, 80, 80);
    path {
      color: white;
      height: 100%;
    }
  }
}

.form-bar {
  margin-top: 500px;
}

.button {
  background-color: rgb(136, 132, 132);
  display: flex;
  padding: 10px;
  flex: 1;

  .button-positive {
    color: white;
    background-color: #3ab77d;
    border-radius: 0pt;
    margin-left: auto;
    text-transform: none;

    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    color: white;
    background-color: #353837;
    border-radius: 0pt;
    text-transform: none;

    &:hover {
      background-color: #8b918f;
    }
  }

  .clear {
    clear: both;
  }
}

.tabBody {
  margin-top: 20px;
  margin-left: 80px;
}
