@import "styles/color.scss";

.root {
  display: flex;
  flex-direction: column;

  .tiles {
    // Confirm if items should be in center
    // justify-content: center;
    margin-bottom: 10px;

    @media only screen and (max-width: 1000px) {
      flex-direction: column;
    }

    &:last-child {
      margin-bottom: unset;
    }

    &.row {
      flex-direction: row;
    }
    // If tile is small
    .task-tray-sm {
      @media only screen and (min-width: 1000px) {
        // flex: 1;
        width: 33.25%;

        .list-item {
          display: flex;
          margin-bottom: 10px;

          .details {
            flex: 1;
            display: flex;
            flex-direction: column;

            .detail-item {
              display: flex;

              .label {
                font-weight: bold;
                color: $color-11;
              }

              .content {
                margin-left: 5px;
              }
            }
          }

          .action {
            display: flex;
            align-items: flex-end;
            font-weight: bold;
          }
        }
      }
    }
    // If tile supposed to be cover the half of the panel
    .task-tray-md {
      @media only screen and (min-width: 1000px) {
        // flex: 1;
        width: 50%;

        .list-item {
          display: flex;
          margin-bottom: 10px;

          .details {
            flex: 1;
            display: flex;
            flex-direction: column;

            .detail-item {
              display: flex;

              .label {
                font-weight: bold;
                color: $color-11;
              }

              .content {
                margin-left: 5px;
              }
            }
          }

          .action {
            display: flex;
            align-items: flex-end;
            font-weight: bold;
          }
        }
      }
    }
    // If tile supposed to be cover the whole panel
    .task-tray-lg {
      @media only screen and (min-width: 1000px) {
        // flex: 1;
        width: 100%;

        .list-item {
          display: flex;
          margin-bottom: 10px;

          .details {
            flex: 1;
            display: flex;
            flex-direction: column;

            .detail-item {
              display: flex;

              .label {
                font-weight: bold;
                color: $color-11;
              }

              .content {
                margin-left: 5px;
              }
            }
          }

          .action {
            display: flex;
            align-items: flex-end;
            font-weight: bold;
          }
        }
      }
    }

    // Tile width 1/3
    .task-tray-third {
      @media only screen and (min-width: 1000px) {
        // flex: 1;
        width: 66.5%;

        .list-item {
          display: flex;
          margin-bottom: 10px;

          .details {
            flex: 1;
            display: flex;
            flex-direction: column;

            .detail-item {
              display: flex;

              .label {
                font-weight: bold;
                color: $color-11;
              }

              .content {
                margin-left: 5px;
              }
            }
          }

          .action {
            display: flex;
            align-items: flex-end;
            font-weight: bold;
          }
        }
      }
    }
  }

  table {
    thead {
      th {
        border-bottom: none;
      }
    }
  }
}

