.action-buttons-wrapper {
    display: flex;
    justify-content: flex-end;
    padding: 10px;
  
    .export-modify-content {
      .export-icon {
        color: #3ab77d;
        transform: scaleX(-1);
        -moz-transform: scaleX(-1);
        -webkit-transform: scaleX(-1);
        -ms-transform: scaleX(-1);
        margin-bottom: -7px;
        margin-right: 5px;
      }
  
      .export-link {
        color: #3ab77d;
        margin-top: -10px;
        margin-right: 15px;
      }
  
      .modify-billing-cycle-icon {
        color: #3ab77d;
        font-size: 16px;
        margin-top: -10px;
        margin-right: 8px;
      }
  
      .modify-billing-cycle-link {
        color: #3ab77d;
        margin-right: 20px;
      }
    }
  }

  .header-container {
    padding: 0;
    margin: 0;
  
    .header {
      align-items: center;
  
      .success-close-button {
        float: right !important;
      }
  
      .title {
        font-weight: bold;
        padding-top: 20px;
        padding-left: 10px;
      }
    }
  }

  .button {
    display: flex;
    padding-top: 20px;

    .button-positive {
      color: white;
      background-color: #3ab77d;
      margin-left: auto;

      &:hover {
        background-color: #0b4930;
      }
    }

    .button-negative {
      color: #3ab77d;
      border-color: #3ab77d;

      &:hover {
        background-color: #3ab77d;
        border-color: white;
        color: white;
      }
    }

    .clear {
      clear: both;
    }
  }

  .atp-fields {
    display: flex;

    .atp-no {
      flex-grow: 1;
    }
    
    .no-of-pages {
      flex-grow: 1;
    }
  }