@import "styles/color.scss";

.root {
  * {
    background-color: unset !important;
  }
  & input {
    background-color: #fff !important;
  }
  div > table:first-child {
    thead {
      tr {
        & th {
          background-color: #e0e0e0 !important;
        }
      }
    }
    tbody {
      tr {
        &:hover {
          td {
            background-color: #eeeeee !important;
          }
        }
        td {
          background-color: #fff !important;
          border: 0 !important;
        }
      }
      & tr:nth-child(even) {
        td {
          background-color: #f5f5f5 !important;
        }
        &:hover td {
          background-color: #eeeeee !important;
        }
      }
    }
  }
  .checkbox {
    text-align: center;
    .icon {
      color: $color-25;
    }
  }
  .sort-field {
    cursor: pointer;
    user-select: none;
    .sort-wrapper {
      .sort-container {
        &.selected {
          color: rgba(0,0,0,.8) !important;
        }
        height: 0;
        color: rgba(0,0,0,.4);
      }
    }
  }
}

.root-table-input {
  .table {
    .table-head {
      .table-head-row {
        .table-head-cell {
          background-color: #e4e4e4 !important;
          &:last-child > button {
            color: $color-13;
          }
          & p {
            font-weight: 500;
          }
        }
      }
    }
  }
}
