.button {
  display: flex;
  padding: 10px;

  .button-positive {
    color: white;
    background-color: #3ab77d;
    margin: 0 auto;


    &:hover {
      background-color: #0b4930;
    }
  }

  .button-negative {
    color: #3ab77d;
    border-color: #3ab77d;

    &:hover {
      background-color: #3ab77d;
      border-color: white;
      color: white;
    }
  }

  .clear {
    clear: both;
  }
}

.success-close-button {
  float: right !important;
  margin-top: -10px;
