@import "styles/color.scss";

.root {
  padding: 10px 15px;

  .column {
    flex-direction: column;
  }

  .label-wrapper {
    margin-right: 25px;
    .label {
      font-weight: 500;
      font-size: 15px;
      user-select: none;
    }
  }

  .radio-wrapper {
    flex-basis: unset;
  }

  .row {
    flex-direction: row;
  }

  .disabled {
    color: rgba(0, 0, 0, 0.5);
  }
}
