@import "styles/color.scss";

.root {
  display: flex;
  flex-direction: row;
  padding: 10px;
  justify-content: flex-end;
  background: #757575;

  button {
    padding: 6px 50px;
    margin-right: 10px;
    text-transform: capitalize;

    &:nth-last-child(1) {
      margin-right: 0px;
    }

    &.danger {
      border-color: #fd5451 !important;
      background-color: #cb6466 !important;
      border: solid 1px;
    }
    &.disapprove {
      border-color: $color-13 !important;
      background-color: #649285 !important;
      border: solid 1px;
    }
  }
  .left {
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }

  .center {
    flex: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }

  .right {
    flex: 2;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  .dark {
    background-color: #424242;
    padding: 6px 50px;

    &:hover {
      background-color: rgba(0, 0, 0, 0.9);
    }
  }
}
