.root {
  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    .read-only-value {
      font-weight: unset;
      padding: 5px;
      min-height: 31px;
    }

    .text-field {
      flex: 1;
      display: flex;

      .input {
        width: 100%;
        input {
          padding: 10px;
          text-align: right;
          &::-webkit-input-placeholder {
            text-align: left;
          }
        }
      }

      .currency {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 40px;
        border-radius: 0 4px 4px 0;
        background-color: #c0c0c0;

        &.grey {
          background-color: #c0c0c0;
        }

        &.outline {
          background-color: transparent;
          color: rgba(0, 0, 0, 0.4);
          border-left: solid 2px rgba(0, 0, 0, 0.3);
        }

        .label {
          padding: 0;
        }
      }

      border: 1px solid transparent;

      &.focus {
        border-color: green;
      }

      // Errors
      &.has-error {
        border-color: red;
        margin-bottom: unset;
      }
    }
    span.error {
      padding-top: 1px;
      color: red;
    }
  }

  .disabled {
    &.currency { border-left: solid 1px; }
    color: rgba(0,0,0,.5);
    background-color: unset !important;
  }

  .text-padding {
    padding-left: 15px;
  }

  .vertical-container {
    height: 100%;
  }

  .lead {
    font-weight: 600;
  }
}
