// SIDE NAV
.avatar{
  width: 130px;
  height: 130px;
}

.container{
  width: 100%;
  height: 100vh;
  display: flex
}

.sidenav{
  height: 100vh;
  background: none;
  width: 170px;
  padding: 20px 10px 0px 0px
}

.sidenavLinks{
  padding-left: 10px
}

.sidenavLink{
  font-size: 16px;
  text-decoration: none;
  font-weight: normal;
  color: #555;
}

.sidenavLinkActive{
  color: #3ab77d;
  font-weight: 700;
  text-decoration: underline;
}

.listView{
  width: 100%;
  height: 100vh;
  flex: 1;
  padding: 10 
}

.listViewOptions{
  display: flex;
  justify-content: space-between
}

.listWrapper{
  width: 100%;
  height: 100vh;
  display: flex;
  padding: 5px;
  margin-top: 20px
}

.dropdown{
  width: 160px;
  padding: 5px
}

.searchTextWrapper{
  flex: 1;
  padding: 5px
}

// DOWNLOADS OPTIONS
.downloadOptionButtonWrapper{
  width: fit-content;
  padding: 5px;
  margin: auto
}

.downloadOptionButton{
  color: #3ab77d;
  vertical-align: middle;
  line-height: 2.5;
  width: fit-content;
  cursor: pointer;
}

.downloadOptionIcon{
  vertical-align: middle;
  background-color: #3ab77d;
  border-radius: 50%;
  color: #fff;
  margin-right: 5px;
  font-size: 15px;
  font-weight: 600;
}

.downloadOptionText{
  font-size: 15px;
  font-weight: 600;
  text-decoration: underline;
}

.downloadPopOver{
  box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12) !important;
}

.downloadPopOverContent{
  padding: 30px 35px 30px 30px;
}

.downloadPopOverButton{
  margin-top: 10px;
  text-align: right;
}

.downloadGroupLabel{
  font-size: 18px;
  font-weight: 500;
  color: #000;
  margin-bottom: 10px;
}

.downloadRadio{
  color: '#555' !important;
  margin: 0;
  padding-top: 3;
  padding-bottom: 3;
  padding-left: 0;
}

.detailModalWrapper{
  width: 500; 
  height: 500; 
  background-color: '#fff'; 
  outline: 'none'
}

.detailModalButton{
  width: 200px;
  margin: auto
}

.detailItemLabel{
  color: #9ea3ac;
  text-transform: uppercase;
}

