.root {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }
  
  .currency {
    display: flex;
    .fields {
      text-align: right;
      align-content: flex-start;
      margin-right: 65px;
      flex: 2;
    }

    .values {
      align-content: flex-end;
    }
  }
}
