.root {
  display: flex;
  width: 100%;
  flex-direction: row;

  .label {
    padding-bottom: 5px;
    margin-right: 20px;
  }

  .wrapper {
    min-width: 150px;
    .read-only-value {
      font-weight: unset;
      padding: 5px;
    }

    .select-container {
      flex: 1;
      display: flex;

      .select {
        padding: 0;
      }

      .input {
        width: 100%;
        .select {
          padding: 10px;
          padding-right: 20px;
          text-align: left;
        }
      }

      border: 1px solid transparent;

      &.focus {
        border-color: green;
      }

      // Errors
      &.has-error {
        border-color: red;
        margin-bottom: unset;
      }

      &.disabled {
        background-color: rgb(223, 223, 223);
      }
    }
    span.error {
      padding-top: 1px;
      color: red;
    }
  }
}
