@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;

  .tiles {
    margin-bottom: 10px;
    @media only screen and (max-width: 1000px) {
      flex-direction: column;
    }

    &:last-child {
      margin-bottom: unset;
    }

    &.row {
      flex-direction: row;
    }

    .task-tray {
      flex: 1;

      .list-item {
        display: flex;
        margin-bottom: 10px;

        .details {
          flex: 1;
          display: flex;
          flex-direction: column;

          .detail-item {
            display: flex;

            .label {
              font-weight: bold;
              color: $color-11;
            }

            .content {
              margin-left: 5px;
            }
          }
        }

        .action {
          display: flex;
          align-items: flex-end;
          font-weight: bold;
        }
      }
    }
  }
}
