@import "styles/color.scss";

.root {
  padding: 10px;
  .action {
    display: flex;
    padding: 10px;
    border-radius: 6px;
    cursor: pointer;
    .icon {
      color: $color-13;
      margin-right: 5px;
    }
    .label {
      color: $color-13;
      user-select: none;
      text-decoration: underline;
    }
    &:hover {
      background: $color-3;
      .icon,
      .label {
        color: $color-9;
      }
    }
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
}
