@import 'styles/color.scss';

.root {
  padding: 20px;

  .row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }

  .column {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  .row,
  .column {
    & > * {
      display: flex;
      flex: 1;
    }
    .subtitle {
      color: $color-11;
      text-transform: uppercase;
    }
    .description {
      color: $color-13;
      font-weight: bold;
      // TODO: Style for description texts
    }
  }
}