@import "styles/color.scss";
.root {
  .search {
    display: flex;
    align-items: center;
    min-width: 400;
    width: 100%;
    border: 1px solid transparent;

    &.focus {
      border-color: gray;
    }

    .input {
      margin-left: 8px;
      flex: 1;
      & input {
        text-overflow: ellipsis;
      }
    }

    .icon {
      padding: 10px;
    }
  }

  .actions {
    height: 100%;
    margin-left: 10px;

    .actions-wrapper {
      .actions-item {
        &:not(:first-child) {
          margin-left: 5px;
        }

        .actions-label {
          margin-left: 5px;
          text-decoration: underline;
          font-size: 14.5px;
          color: $color-3;
          text-transform: none
        }

        .actions-icon {
          color: $color-3;
        }
      }
    }
  }

  .reply {
    transform: scaleX(-1);
  }
}
