        font-size: 14px;
        padding: 5px 30px 5px 30px;
        font-weight: 400;
        width: 230px;
      }

      .transaction-type-item-icon-invoice {
        font-size: 18px;
        margin-bottom: -3px;
        color: #1ab1c4;
      }

      .transaction-type-item-icon-credit-memo {
