        margin-bottom: -3px;
        color: #fab435;
      }

      .transaction-type-item-icon-debit-note {
        font-size: 18px;
        margin-bottom: -3px;
        color: #fd5451;
      }

      .transaction-type-item-icon-credit-note {
        font-size: 18px;
        margin-bottom: -3px;
        color: #3ab77d;
      }
    }
  }

  .reference-number-container {
    width: 620px;
    display: flex;
    margin-top: 40px;

    .reference-number-field {
      flex: 1;
    }

    .reference-number-value {
      margin-top: -15px;
    }
  }

  .advanced-search-link {
    color: #3ab77d;
    margin-left: 505px;
  }
}


//for modify billing cycle,creatinvoice component
.modify-bill-run-inclusion {
  display: flex;
  justify-content: flex-end;
  padding: 10px;

  .modify-bill-run-inclusion-content {
    .export-icon {
      color: #3ab77d;
      transform: scaleX(-1);
      -moz-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
      margin-bottom: -7px;
      margin-right: 5px;
    }

    .export-link {
      margin-top: -10px;
      margin-right: 15px;
      color: #3ab77d;
    }

    .view-icon {
      color: #3ab77d;
      margin-bottom: 0;
      margin-right: 8px;
    }

    .view-link {
      color: #3ab77d;
      margin-right: 20px;
    }
  }
}
.button-wrapper {
  display: inline;
  .button {
    background-color: rgb(136, 132, 132);
    display: flex;
    padding: 10px;
    .item-right-side {
      border-radius: 3pt;
      margin-left: auto;
    }
    .button-positive {
      color: white;
      background-color: #3ab77d;
      width: 150px;
      margin-right: 10px;
      text-transform: none;
      &:hover {
        background-color: #0b4930;
      }
    }
    .button-negative {
      color: white;
      background-color: #353837;
      border-radius: 3pt;
      width: 300 !important;
      text-transform: none;
      &:hover {
        background-color: #8b918f;
      }
    }
  }
}

