.root {
    width: 100%;  
    
    .paper {
      width: 100%;
  
      .pagination {
        width: 20%;
        margin-left: 80%;
      }
  
      .table-wrapper {
        overflow-x: hidden;
        overflow-y: auto;
        height: 100%;
        border-radius: 5px 5px 0px 0px;
  
        .table {
          min-width: 100%;
          .table-head{
            background-color: #e0e0e0;
            .th{
              background-color:#e0e0e0 !important;
              &:first-child, &:last-child {
                min-width: 125px;
              }
            }
          }
  
          .sort-icon {
            height: 20px;
            font-size: 13px;
            float: right;
            cursor: pointer
          }
  
          .column-header-checkbox{
            margin-bottom: '-10px'; color: "rgba(0, 0, 0, 0.54)"
          }
        }
      }
    }
  }
  