@import "styles/color.scss";

.root {
  .action-container {
    height: 100%;
  }

  .item-container {
    cursor: pointer;
    overflow: hidden;

    &.active {
      border-bottom: solid 1px $color-13;
    }

    * {
      user-select: none;
    }

    .title {
      font-weight: 600 !important;
      &.active {
        color: $color-13;
      }
    }

    .details {
      color: rgba(0, 0, 0, 0.8);
      font-size: 14px;
    }

    .action-remove-container {
      position: relative;
      & > .action-remove {
        cursor: pointer;
        padding: 0;
        position: absolute;
        top: -5px;
        left: 5px;
        transform: scale(0.7);
      }
    }
  }

  .summary-container {
    height: 100%;
    padding: 0 10px;
    .summary-wrapper {
      padding: 0 20px;
    }
  }

  .highlight {
    color: rgb(0, 0, 0) !important;
  }

  .bold {
    font-weight: 600 !important;
  }
}
