@import "styles/color.scss";

.root {
    flex-grow: 1;
    display: flex;
  .header {
    background-color: $color-22;
    padding: 20px 10px;
    & h5 {
      font-weight: 500;
      color: rgba(122, 121, 121, 0.8);
      letter-spacing: 0.1px;
    }
  }
  .search {
    margin: 15px 0px;
  }
  .tab-selected {
    color: $color-3;
    font-weight: 600;
    border-bottom: 2px solid $color-3;
  }
}