@import "styles/color.scss";

.root {
  .title {
    &.center {
      text-align: center;
    }
    .text-container {
      height: 100%;
      .label {
        user-select: none;
      }
    }
  }
  .wrapper {
    position: relative;
    border: solid 1px rgba(0,0,0,.2);
    border-radius: 5px;
    padding: 8px 0;
    &.error {
      border-color: $color-17;
    }
    .text {
      user-select: none;
      .background {
        color: rgba(0,0,0,.4);
      }
      .disabled {
        color: rgba(0,0,0,.4);
      }
    }
    .icon {
      color: rgba(0,0,0,.4);
    }
    .date-picker {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      opacity: 0;
      &::-webkit-calendar-picker-indicator {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        color: transparent;
        background: transparent;
        z-index: 1;
      }
      &::-webkit-inner-spin-button {
        z-index: 1;
      }
      &::-webkit-clear-button {
        z-index: 1;
      }
    }
  }

  .text-padding {
    padding-left: 15px;
  }

  .vertical-container {
    height: 100%;
  }

  .lead {
    font-size: 14px;
    text-align: left;
    font-weight: 600;
  }

  .form {
    font-size: 14px;
    font-weight: 600;
  }

  .error-wrapper {
    .error {
      padding-top: 1px;
      color: $color-17;
    }
  }
}
