.drawer-content {
  margin: 20px 0px 20px 0px;
}

.content-header-details {
  display: flex;

  .fields {
    margin-right: 20px;
    flex: 1;
  }

  .values {
    margin-right: 8px;
  }

  .status {
    color: #fd5451;
  }
}

.client_details {
  padding: 20pt;
  margin-bottom: 40px;

  .client-details-wrapper {
    .corporate-name-wrapper {
      .corporate-name-value {
        font-size: 20px;
        font-weight: bold;
      }
    }

    .basic-hmo-information-wrapper {
      display: flex;

      .left-container {
        flex: 1;

        .information-title {
          font-weight: bold;
          margin-top: 10px;
        }

        .basic-information-wrapper {
          display: flex;

          .field {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
            }
          }

          .value {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
              font-weight: 450;
            }
          }
        }
      }

      .center-container {
        flex: 1;
        margin-top: 34px;

        .basic-information-wrapper {
          display: flex;

          .field {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
            }
          }

          .value {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
              font-weight: 450;
            }
          }
        }
      }

      .right-container {
        flex: 1;

        .information-title {
          font-weight: bold;
          margin-top: 10px;
        }

        .hmo-information-wrapper {
          display: flex;

          .field {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
            }
          }

          .value {
            flex: 1;

            .text {
              margin: 5px 0px 5px 0px;
              font-weight: 450;
            }
          }
        }
      }
    }
  }

  .title {
    margin-bottom: 5px;
    margin-top: 10px;
  }
  .subtitle {
    margin-left: 0px;
  }
}

.export-view-billing-cycle-wrapper {
  display: flex;
  justify-content: flex-end;
  padding: 10px;

  .export-view-content {
    .export-icon {
      color: #3ab77d;
      transform: scaleX(-1);
      -moz-transform: scaleX(-1);
      -webkit-transform: scaleX(-1);
      -ms-transform: scaleX(-1);
      margin-bottom: -7px;
      margin-right: 5px;
    }

    .export-link {
      margin-top: -10px;
      margin-right: 15px;
      color: #3ab77d;
    }

    .view-icon {
      color: #3ab77d;
      margin-bottom: 0;
      margin-right: 8px;
    }

    .view-link {
      color: #3ab77d;
      margin-right: 20px;
    }
  }
}

.button {
  background-color: rgb(136, 132, 132);
  display: flex;
  padding: 10px;
  z-index: 2;
  .item-right-side {
    border-radius: 3pt;
    margin-left: auto;
  }
  .button-positive {
    color: white;
    background-color: #3ab77d;
    width: 150px;
    margin-right: 10px;
    &:hover {
      background-color: #0b4930;
    }
  }
  .button-negative {
    color: white;
    background-color: rgba(214, 58, 23, 0.705);
    border-radius: 3pt;
    width: 150px !important;
    margin-right: 20px;

    &:hover {
      background-color: rgba(179, 50, 21, 0.521);
    }
  }
    .button-close {
      color: white;
      background-color: #353837;
      border-radius: 3pt;
      width: 150px !important;
  
      &:hover {
        background-color: #8b918f;
      }
  }
}
