@import "styles/color.scss";

.root {
  .action {
    &.view-more {
      color: $color-13;
      text-decoration: underline;
      white-space: nowrap;
    }
  }
  .header-text {
    font-weight: 450;
    margin: 10px 0;
  }

  .text {
    padding: 5px;
    &.value {
      color: black;
    }
  }

  .no-overflow {
    max-width: 250px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .radio {
    color: $color-1;
    &:checked {
      color: $color-1;
    }
  }

  .page-action {
    letter-spacing: 1px;
    color: rgba(0, 0, 0, 0.8);
    border-radius: 0;
  }

  .active {
    color: $color-13;
    border-bottom: solid 3px $color-13;
  }

  .collection-instructions-container {
    padding: 30px;
    color: rgba(0, 0, 0, 0.5);
    .container {
      &:first-child {
        margin: 0 20px 20px 0;
      }
    }
    .icon {
      font-size: 13px;
      margin-right: 5px;
      color: rgba(0, 0, 0, 0.9);
    }
    .indent {
      margin-left: 1em;
    }
    .uppercase {
      text-transform: uppercase;
    }
  }
}
