.totalBarWrapper{
  background-color: #fff;
  height: 130px;
  width: 100%;
  display: flex;
  border-radius: 5px;
  box-shadow: 1px 2px 5px #ddd;
}

.totalAllWrapper{
  display: inline-block;
  height: 100%;
  width: 220px;
  padding: 25px;
  box-shadow: 5px 0 5px -2px #ddd;
}

.totalAllHeader{
  color: #48b77c;
}

.totalAgeWrapper{
  display: inline-block;
  height: 100%;
  flex: 1;
  padding-left: 20px;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  vertical-align: middle;
}

.totalAgeItem{
  display: inline-block;
  vertical-align: middle;
  height: 130px;
  width: 180px;
  padding: 20px;
}

.totalCollectibleValue{
  font-size: 22px;
  font-weight: 800;
  color: #48b77c
}

.totalValueFirst{
  font-size: 22px;
  font-weight: 500;
  text-align: center;
}

.totalValue{
  font-size: 22px;
  font-weight: 500;
  text-align: center;
  color: #f65250;
}

.totalValueStatus{
  width: 100%;
  font-size: 15px;
  height: 20px;
  text-align: center;
  color: #f65250;
}

.totalValueSubtitle{
  font-size: 15px;
  margin-top: -5px;
  text-align: center;
  color: #999
}


