@import 'styles/color.scss';

.root {
  width: 100%;

  .label {
    padding-bottom: 5px;
    margin-right: 5px;
  }

  .disabled {
    color: rgba(0,0,0,.5);
    background-color: unset !important;
  }

  .wrapper {

    .read-only-value {
      font-weight: unset;
      padding: 5px;
      min-height: 31px;
    }

    .text-field {
      &.no-margin {
        margin-bottom: 0px;
      }
      margin-bottom: 23px;

      .form-control {
        width: 100%;
      }

      .input {
        width: 100%;
        * {
          padding: 10px;
        }
      }

      .input-amount {
        width: 100%;
        * {
          padding: 10px;
          text-align: right;
        }
      }

      .end-adornment {
        color: #8b8b8b;
        font-weight: 500;
        padding: 10px;
      }

      .select {
        color: #000000;
        padding: 4px;
        padding-left: 10px;
      }

      border: 1px solid transparent;

      &.focus {
        border-color: green;
      }

      // Errors
      &.has-error {
        border-color: red !important;
        margin-bottom: unset;
      }
    }

    .error {
      padding-top: 1px;
      color: red;
    }
  }

  .text-padding {
    padding-left: 15px;
  }

  .vertical-container {
    height: 100%;
  }

  .lead {
    font-weight: 600;
  }
}
