.root {
  position: relative;
}

.btn {
  z-index: 10;
  display: flex;
  color: white;
  background-color: #3ab77d;
  text-transform: initial;
  padding: 7px 20px;
  &:hover {
    background-color: #0b4930;
  }
}

.form-control {
  z-index: -10;
  position: absolute;
  top: 50px;
  right: 0;
  width: 300px;
}
